[data-tooltip] {
  position: relative;
}
[data-tooltip]:before {
  background: #616161;
  border-radius: 2px;
  color: #fff;
  content: attr(data-tooltip);
  font-size: 12px;
  display: inline-block;
  opacity: 0;
  padding: 5px 8px;
  position: absolute;
  pointer-events: none;
  text-transform: initial;
  -webkit-transition: 0.15s cubic-bezier(0.25, 0.8, 0.5, 1);
  transition: 0.15s cubic-bezier(0.25, 0.8, 0.5, 1);
  width: auto;
