.md {
  font-size: 15px;
  color: #34495e;
  line-height: 1.6em;
}
.md h1,
.md h2,
.md h3,
.md h4 {
  font-weight: bold;
  color: #2c3e50;
  margin-left: -3px;
  line-height: 1.6em;
}
.md hr {
  border: 0;
  background-color: #eee;
  height: 1px;
  margin: 16px 0;
}
.md h1 {
  font-size: 30px;
  border-bottom: #eee 1px solid;
  padding-bottom: 8px;
}
.md h2 {
  font-size: 24px;
  border-bottom: #eee 1px solid;
  padding-bottom: 8px;
}
.md h3 {
  font-size: 20px;
}
.md h4 {
  font-size: 16px;
}
.md blockquote {
  border-left-color: #009688;
  padding: 16px 10px 16px 24px;
  margin: 24px 0px;
  background: #f3f3f3;
  font-size: 15px;
  font-weight: normal;
/*color:#214750;*/
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
}
.md blockquote p {
  margin: 0;
}
@media screen and (max-width: 900px) {
.md blockquote {
    padding: 8px 10px 8px 15px;
    font-size: 14px;
}
}
.md ol,
.md ul {
  margin: 20px 0;
}
.md li {
  line-height: 2em;
}
.md pre {
  position: relative;
  margin-bottom: 24px;
  border-radius: 3px;
  background-color: #f6fb6f 6;
  white-space: pre;
}
.md code {
  display: block;
  padding: 12px 24px;
  font-family: "SFMono-Regular", Menlo, 'Roboto Mono', Monaco, courier, monospace;
  font-size: 13px;
  font-weight: normal !important;
  -webkit-font-smoothing: initial;
  background-color: #f6f6f6 !important;
  line-height: 1.8em;
  margin: 10px 0;
  white-space: pre;
  overflow-x: auto,;
}
.md code:after,
.md code:before,
.md kbd:after,
.md kbd:before {
  content: "" !important;
}
.md table {
  border: #ccc 1px solid;
  margin-bottom: 10px;
  width: 100%;
  border-collapse: collapse;
}
.md th {
  border: #ccc 1px solid;
  text-align: left;
  background: #eee;
  padding: 8px 12px;
}
.md td {
  border: #ccc 1px solid;
  padding: 8px 12px;
}
.md tr:nth-child(even) {
  background-color: #f6f6f6;
}


